







































































import DataTable from '@/components/DataTable.vue';
import VueMarkdown from 'vue-markdown';

export default {
  name: "PageMyTable",
  components: {
    DataTable,
    VueMarkdown,
  },
  data() {
    return {
      items: [{id: 30050, name: 'Cleopatra', surname: 'Petrova', age: 12},
        {id: 30051, name: 'Alex', surname: 'Green', age: 15},
        {id: 30052, name: 'Ann', surname: 'Ally', age: 1},
        {id: 30053, name: 'Wolf', surname: 'Nilson', age: 5}],
      columns: [{title: '№', value: '_index'},
        {title: 'Name', value: 'name'},
        {title: 'Surname', value: 'surname', sortable: true},
        {title: 'Age', value: 'age', type: 'number', sortable: true}],
      search: {
        fields: ['name', 'surname'],
        filters: [
            (v: string): string => v.toLowerCase(),
            (v: string) => toKeyboardLayoutRuEn(v.toLowerCase()),
        ],
      },
      propsitems: [{
        props: 'items', type: 'string',
        description: 'Items is the table data in array format'
      },
        {
          props: 'columns', type: 'string',
          description: 'The table columns headings'
        },
        {
          props: 'search', type: 'fields: string[];\n' +
            'filters: Array<(v: string) => string',
          description: 'Contains fields that will be searched (filtered) and additional functions'
        },
      ],
      propscolumns: [{title: '№', value: '_index'},
        {title: 'Props', value: 'props'},
        {title: 'Type', value: 'type'},
        {title: 'Description', value: 'description'}],
    }
  }
}

function toKeyboardLayoutRuEn(str:string) {
  let associativeArray: { [symb: string]: string } = {
    'q': 'й', 'w': 'ц', 'e': 'у', 'r': 'к', 't': 'е', 'y': 'н', 'u': 'г',
    'i': 'ш', 'o': 'щ', 'p': 'з', '[': 'х', ']': 'ъ', 'a': 'ф', 's': 'ы',
    'd': 'в', 'f': 'а', 'g': 'п', 'h': 'р', 'j': 'о', 'k': 'л', 'l': 'д',
    ';': 'ж', "'": 'э', 'z': 'я', 'x': 'ч', 'c': 'с', 'v': 'м', 'b': 'и',
    'n': 'т', 'm': 'ь', ',': 'б', '.': 'ю', '/': '.'
  };
  return str.replace(/[A-z/,.;'\]\[]/g, (x) =>
     x === x.toLowerCase() ? associativeArray[x] : associativeArray[x.toLowerCase()].toUpperCase()
  );
}
